import React, { useState, useEffect, useRef, useCallback } from "react"
import styled from "styled-components"

const PostLink = ({
  index,
  project,
  projectIsOpen,
  currentProjectHeight,
  setCurrentProjectHeight,
  setCurrentSlide,
  projectBreakPoints,
  bioHeight,
  viewportWidth,
}) => {
  const [contentHeight, setContentHeight] = useState(0)
  const projectContainer = useRef(null)

  // When the project renders get the height of all the content in this project
  useEffect(() => {
    setContentHeight(projectContainer.current.scrollHeight + 48)
  }, [projectContainer, setContentHeight, viewportWidth])

  // When the project is opened, set the height so other project can handle their position
  useEffect(() => {
    if (index === projectIsOpen) {
      setCurrentProjectHeight(contentHeight)
    }
  }, [contentHeight, projectIsOpen, setCurrentProjectHeight, index])

  const openProject = () => setCurrentSlide(projectBreakPoints[index])

  const closeProject = () => setCurrentSlide(0)

  return (
    <Container
      ref={projectContainer}
      contentHeight={contentHeight}
      index={index}
      currentProjectHeight={currentProjectHeight}
      projectIsOpen={projectIsOpen}
      bioHeight={bioHeight}
    >
      <Header
        onClick={() => {
          projectIsOpen === null
            ? openProject()
            : projectIsOpen === index
            ? closeProject()
            : openProject()
        }}
      >
        <GridItem>
          <InlineContainer>
            {project.frontmatter.deliverable}{" "}
            {project.frontmatter.new && <NewIndicator>New</NewIndicator>}
          </InlineContainer>
          <PracticeContainer>
            {project.frontmatter.practice.map((practice) => (
              <PracticeIndicator
                key={practice}
                practice={practice}
                projectIsOpen={projectIsOpen}
              />
            ))}
          </PracticeContainer>
        </GridItem>

        <GridItem>
          <Deliverable>{project.frontmatter.title}</Deliverable>
          <InlineContainer>
            <Year>{project.frontmatter.date.slice(-4)}</Year>
            <Arrow
              projectIsOpen={projectIsOpen}
              index={index}
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 20 20"
            >
              <g>
                <g transform="translate(19.584 19.584) rotate(180)">
                  <rect
                    width="20"
                    height="20"
                    rx="10"
                    transform="translate(0 0)"
                    fill="transparent"
                  />
                  <g transform="translate(3.112 17.143) rotate(-90)">
                    <image
                      width="10.102"
                      height="10.102"
                      transform="translate(7.143 0) rotate(45)"
                      href="data:image/png;base64,"
                    />
                  </g>
                </g>
              </g>
            </Arrow>
          </InlineContainer>
        </GridItem>
      </Header>
      <MetaData>
        <InfoContainer>
          <span>Partner</span> <span>{project.frontmatter.partner}</span>
        </InfoContainer>
        <InfoContainer>
          <span>Practice</span>{" "}
          <span>
            {project.frontmatter.practice.map((practice, index) =>
              index === project.frontmatter.practice.length - 1
                ? `${practice}.`
                : `${practice}, `
            )}
          </span>
        </InfoContainer>
        <InfoContainer>
          <span>{project.frontmatter.ongoing && "Last"} Release</span>{" "}
          <span>{project.frontmatter.date}</span>
        </InfoContainer>
        <InfoContainer>
          <span>Status</span>{" "}
          <span>{project.frontmatter.ongoing ? "Ongoing" : "Complete"}</span>
        </InfoContainer>
        {project.frontmatter.url && (
          <InfoContainer>
            <span>Access</span>{" "}
            <span>
              <a href={project.frontmatter.url} target="_blank">
                See The Project
              </a>
            </span>
          </InfoContainer>
        )}
      </MetaData>
      <Content dangerouslySetInnerHTML={{ __html: project.html }} />
    </Container>
  )
}

const Container = styled.div`
  height: ${({ projectIsOpen, index, contentHeight }) =>
    index === projectIsOpen ? contentHeight : 32}px;
  position: relative;
  top: ${({ projectIsOpen, index, currentProjectHeight }) =>
    // IF THIS IS THE PROJECT THAT IS OPEN
    index === projectIsOpen
      ? // MOVE IT ABOVE ALL THE PROJECTS
        32 * -index
      : // IF NO PROJECT IS OPEN
      projectIsOpen === null
      ? // LEAVE THEM ALL ALONE
        0
      : index < projectIsOpen
      ? // IF BEFORE THE CURRENT OPEN PROJECT, MOVE BELOW THE OPEN PROJECT
        currentProjectHeight
      : 0}px;
  min-height: 32px;
  overflow: hidden;
  border-top: 1px solid black;
  background: #f6f6f5;
  transition: 0.7s cubic-bezier(0.24, 1, 0.32, 1);
  @media (max-width: 800px) {
    padding: 0 10px;
  }
`

const Header = styled.div`
  display: grid;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  grid-template-columns: 7fr 8fr;
  grid-column-gap: 5px;
  align-items: center;
  cursor: pointer;
  &:hover {
    > div {
      color: blue;
    }
  }
  @media (max-width: 500px) {
    grid-template-columns: 1fr min-content;
  }
`

const GridItem = styled.div`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 20px;
  line-height: 22px;
  font-family: "MonumentMono";
  letter-spacing: -0.025em;
  padding: 4px 0 6px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  @media (max-width: 500px) {
    &:nth-child(1) {
      justify-content: space-between;
    }
    &:nth-child(2) {
      justify-content: flex-end;
    }
  }
`

const InlineContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
`

const Deliverable = styled.span`
  @media (max-width: 500px) {
    text-align: right;
  }
`

const Year = styled.span`
  @media (max-width: 500px) {
    display: none;
  }
`

const Arrow = styled.svg`
  transform: rotate(
    ${({ projectIsOpen, index }) => (index === projectIsOpen ? 90 : 0)}deg
  );
  background-color: ${({ projectIsOpen, index }) =>
    index === projectIsOpen ? "red" : "#cccccc"};
  border-radius: 100px;
  padding-top: 1px;
  padding-left: 1px;
  width: 20px;
  height: 20px;
  margin-left: 5px;
  @media (max-width: 800px) {
    display: none;
  }
`

const Content = styled.div`
  background: #f6f6f5;
  img {
    width: 100%;
  }
  h3 {
    width: 100%;
    margin: 0 0 10px;
    font-family: "Monument";
    font-size: 24px;
    line-height: 30px;
  }
  p {
    font-size: 16px;
    line-height: 20px;
    font-family: "MonumentMono";
    letter-spacing: -0.025em;
    margin: 15px 0;
  }
  a {
    color: black;
    &:visited {
      color: black;
    }
  }
`

const MetaData = styled.div`
  background: #f6f6f5;
  font-family: MonumentMono;
  letter-spacing: -0.025em;
  margin-bottom: 10px;
  border-bottom: 1px dotted black;
`

const InfoContainer = styled.div`
  display: grid;
  grid-template-columns: 7fr 8fr;
  border-top: 1px dotted black;
  padding: 3px 0 1px;
  grid-column-gap: 5px;
  span {
    font-size: 16px;
    line-height: 18px;
    padding-bottom: 4px;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  a {
    color: blue;
  }
  a:visited {
    color: #ff0000;
  }
`

const PracticeContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  @media (max-width: 500px) {
    // display: none;
  }
`

const PracticeIndicator = styled.div`
  width: 8px;
  height: 8px;
  padding: 0px;
  border-radius: 20px;
  background: ${({ practice }) => {
    let color
    if (practice === "Strategy") {
      color = "#aaaaaa"
    }
    if (practice === "Design") {
      color = "#ffaf00"
    }
    if (practice === "Engineering") {
      color = "#ff0000"
    }
    if (practice === "Art Direction") {
      color = "#0092ff"
    }
    if (practice === "Photography") {
      color = "#05e900"
    }
    return color
  }};
  margin-left: 2px;
`

const NewIndicator = styled.div`
  padding: 1px 3px 2px 2px;
  margin-left: 5px;
  border-radius: 2px;
  background: #0092ff;
  color: white;
  font-size: 14px;
  line-height: 14px;
  height: 17px;
  align-self: center;
  @media (max-width: 500px) {
    display: none;
  }
`

export default PostLink
