import React from 'react';
import styled from 'styled-components';

const Arrows = function({
  next,
  previous
}) {

  return (
    <Container>
      <Arrow onClick={previous} ><Svg><image width="15" height="15" transform="translate(5 15) rotate(-45)" href="data:image/png;base64,"/></Svg></Arrow>
      <Arrow onClick={next} right><Svg><image width="15" height="15" transform="translate(5 15) rotate(-45)" href="data:image/png;base64,"/></Svg></Arrow>
    </Container>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: row;
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 100000;
  cursor: pointer;
  @media(max-width: 800px) {
    position: absolute;
    top: 478px;
  }
  @media(max-width: 500px) {
    position: absolute;
    top: 338px;
  }
`;

const Arrow = styled.div`
  margin-left: ${({ right }) => right ? 2 : 0}px;
  transform: rotate(${({ right }) => right ? 180 : 0}deg);
  border-radius: 15px 0 0 15px;
  background: #0a0a0a;
  width: 30px;
  height: 30px;
  transition: 0.2s cubic-bezier(0.24, 1, 0.32, 1);
  &:hover {
    transform: rotate(${({ right }) => right ? 180 : 0}deg) scale(0.95);
  }
  &:active {
    background: grey;
    outline: none;
  }
`;

const Svg = styled.svg`
  width: 30px;
  height: 30px;
`;

export default Arrows;